                    this.$message({
                        message: '!',
                        type: 'warning'
                    });
                    return false
                }
                if (this.baseForm.district == '') {
                    this.$message({
                        message: '!',
                        type: 'warning'
                    });
                    return false
                }
                //
                //
                var companyName = this.baseForm.companyName;
                //
                var projectName = this.baseForm.projectName;
                //
                var projectContent = this.baseForm.projectContent;
                //
                var projectType = this.baseForm.projectType;
                //
