                var investmentSelfPrepare = this.total.investmentSelfPrepare;
                var investmentLoans = this.total.investmentLoans;
                var investmentUsingForex = this.total.investmentUsingForex;
                //2018()
                var completedInvestment = this.lastYear.completedInvestment;
                var completedInvestmentLoans = this.lastYear.completedInvestmentLoans;
                var completedInvestmentSelfPrepare = this.lastYear.completedInvestmentSelfPrepare;
                //2019()
                var projectedInvestment = this.currentYear.projectedInvestment;
                var projectedLoans = this.currentYear.projectedLoans;
                var projectedSelfPrepare = this.currentYear.projectedSelfPrepare;
                //
                var newSalesRevenue = this.expectedReturn.newSalesRevenue;
                var newProfits = this.expectedReturn.newProfits;
                var newTax = this.expectedReturn.newTax;
                var newForex = this.expectedReturn.newForex;
                //
                var startDate = this.subjectYear.startDate.getFullYear();
                var endDate = this.subjectYear.endDate.getFullYear();
                var that = this;
                var baseUrl = that.$store.state.url;
                this.$axios.post('industrialdeclaration/addDeclaration', this.$qs.stringify({
                    //
                    declarationType: this.$store.state.declarationType,            //
                    isNew: this.$store.state.isNew,            //
                    declarationIndustry: this.$store.state.declarationIndustry,            //
                    industryTypeDetail: this.$store.state.industryTypeDetail,            //

                    //
                    //
                    companyName: this.baseForm.companyName,      //
                    //
                    projectName: this.baseForm.projectName,       //
                    //
                    projectContent: this.baseForm.projectContent,  //
                    //
                    'projectType': this.baseForm.projectType,
                    //
                    'projectTypeDetail': this.baseForm.projectTypeDetail,
                    //
                    province: this.baseForm.province, //
                    //
                    city: this.baseForm.city,     //
                    //
                    district: this.baseForm.district,    //
                    //
                    //
                    'detailEntity.advancedManufacturingType': this.advancedManufacturingType,
                    //2018()
                    'detailEntity.completedInvestment': this.lastYear.completedInvestment,
                    'detailEntity.completedInvestmentLoans': this.lastYear.completedInvestmentLoans,
                    'detailEntity.completedInvestmentSelfPrepare': this.lastYear.completedInvestmentSelfPrepare,
                    //2019()
                    'detailEntity.projectedInvestment': this.currentYear.projectedInvestment,
                    'detailEntity.projectedLoans': this.currentYear.projectedLoans,
                    'detailEntity.projectedSelfPrepare': this.currentYear.projectedSelfPrepare,
                    //()
                    totalInvestment: this.total.totalInvestment,              //
                    'detailEntity.investmentSelfPrepare': this.total.investmentSelfPrepare,
                    'detailEntity.investmentLoans': this.total.investmentLoans,
                    'detailEntity.investmentUsingForex': this.total.investmentUsingForex,
                    //
                    'detailEntity.newSalesRevenue': this.expectedReturn.newSalesRevenue,
                    'detailEntity.newProfits': this.expectedReturn.newProfits,
                    'detailEntity.newTax': this.expectedReturn.newTax,
                    'detailEntity.newForex': this.expectedReturn.newForex,
                    //
                    startDate: this.subjectYear.startDate.getFullYear(),     //
                    endDate: this.subjectYear.endDate.getFullYear(),         //
                }))
                    .then((res) => {
                        if (res.data.code == 0) {
                            that.$message.success(res.data.msg);
                            that.$router.push('/iHome/IndusChart')
                        } else {
                            this.$message({
                                message: res.data.msg,
                                type: 'warning'
                            });
                        }
                    })
                    .catch(err => {
                        this.$message.error('');
                    })

            },
            //
            handleChange1() {
                this.baseForm.projectType = this.val[0];
                this.baseForm.projectTypeDetail = this.val[1];
            },
            //
            handleChange(value) {
                var arr = [];
                value.forEach((item, index) => {
                    arr.push(CodeToText[item])
                })
                this.form.city = arr;
                // if (arr[1] == '') {
                //     this.baseForm.city = arr[0];
                // } else {
                //     this.baseForm.city = arr[1];
                // }
                this.baseForm.province = arr[0]
                this.baseForm.city = arr[1];
                this.baseForm.district = arr[2];

            },
