                var that = this;
                var id = that.$store.state.companyId;
                that.$axios.get('bussinessCenter/findBussinessCenter',{
                    params:{
                        id:id
                    }
                })
                    .then(res=>{
                        console.log(res)
                        if (res.data.code==0){
                            console.log(res.data.data)
                            that.form.firmName   = res.data.data.firmName;
                            that.form.enterpriseProfile   = res.data.data.enterpriseProfile
